                  <div class="invalid-feedback">
                    <span id="termandcondcheck-msg"></span>
                  </div>
                </div>
              </div>
            </div>
            <div v-if="detalles.mailingUnwantedText"  class="col-sm-12 mt-2 mb-2 ">
                  <div class="form-check text-left">
                    <input type="checkbox" class="form-check-input" id="unmailingcheck" v-model="unmailing">
                    <label class="form-check-label" for="unmailingcheck" v-html="detalles.mailingUnwantedText"/>
                  </div>
                </div>
            <!--<div class="col-sm-6  col-md-6 col-lg-6 ">
                    <button class="btn btn-primary align-middle "  v-on:click="deleteAll"><span class="align-middle">delete  &nbsp; <i class="large material-icons align-middle">remove_shopping_cart</i></span></button>
            </div>-->
            <div class="col-sm-12 col-md-12 col-lg-12 text-center">
              <div v-if="captchaValidation">
                <div v-if="recaptchatoken" class="button-container">
                  <vue-recaptcha
                    :sitekey="recaptchatoken"
                    ref="recaptcha"
                    @verify="onCaptchaVerified"
                    @expired="onCaptchaExpired"
                    size="invisible"
                  >
                    <button class="btn btn-primary align-middle" v-on:click="verify">
                      <span class="align-middle">
                        {{multilanguage.book[language]}}&nbsp;
                        <i class="large material-icons align-middle">attach_money</i>
                      </span>
                    </button>
                  </vue-recaptcha>
                </div>
                <div v-else class="button-container">
                  <div v-if="reseolvedCaptcha" class="col-sm-12 text-center">
                    <button class="btn btn-primary align-middle" v-on:click="book('')">
                      <span class="align-middle">
                        {{multilanguage.book[language]}}&nbsp;
                        <i class="large material-icons align-middle">attach_money</i>
                      </span>
                    </button>
                  </div>
                  <div v-else class="row">
                    <div class="col-sm-12">
                      <div>
                        <strong class="text-center">{{multilanguage.norobot[language]}}</strong>
                      </div>
                      <div class="qtcaptcha">
                        <my-captcha :callSuccess="captchaOk" color="grey" resolve="text"></my-captcha>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else class="col-sm-12 text-center button-container">
                <button class="btn btn-primary align-middle" v-on:click="book('')">
                  <span class="align-middle">
                    {{multilanguage.book[language]}} &nbsp;
                    <i class="large material-icons align-middle">attach_money</i>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-lg-4">
          <div id="resume" class="card shadow-sm">
            <div class="card-header title bg-primary text-white">{{multilanguage.details[language]}}</div>
            <div id="accordion">
              <div v-for="(c,index) in cart" :key="index">
                <div
                  class="card-header title-service"
                  :id="'heading' + index"
                  data-toggle="collapse"
                  :data-target="'#collapse' + index"
                  aria-expanded="true"
                  aria-controls="collapseOne"
                  @click="activestyle = index"
                >
                  # {{index + 1}}
                  <i :class="seticon(c.bookingtype)"></i>
                  {{multilanguage.servicedetail[language]}}{{c.bookingtype}}
                </div>
                <div
                  :id="'collapse' + index"
                  class="collapse"
                  :class="{'show2' : activedetail === index }"
                  :aria-labelledby="'heading' +index"
                  data-parent="#accordion"
                >
                  <div class="card-body">
                    <div v-html="c.itemDescription"></div>
                  <div id="desglose" v-if="!nobreakdown">
                  <div class="content-object">
                    <div class="content-tittle-table">{{multilanguage.priceline[language]}}</div>

                    <div v-if="c.prices && c.prices.length > 0">
                      <table class="table" width="100%">
                        <tr v-for="(p,index) in c.prices" v-bind:key="index">
                          <td>{{p.description}}</td>
                          <td width="115px" class="text-right">
                            <span
                              style="display: inline-block;"
                            >{{p.retailPrice.value }} {{p.retailPrice.currencyIsoCode }}</span>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>

                <div
                  id="lineapagos"
                  v-if="c.paymentLines && c.paymentLines.length > 0"
                >
                  <div class="content-object">
                    <div class="content-tittle">{{multilanguage.paymentline[language]}}</div>

                    <dl>
                      <dd v-for="(p,index2) in c.paymentLines" v-bind:key="index2">
                        <span>
                          <span
                            class="font-weight-bold"
                          >{{p.amount.value }} {{p.amount.currencyIsoCode }}</span>
                          {{multilanguage.through[language]}}
                          <span class="font-weight-bold">{{p.paymentMethod}}</span>
                           {{multilanguage.until[language]}}
                          <span class="font-weight-bold">{{formatDatetoview(p.date)}}</span>
                        </span>
                      </dd>
                    </dl>
                  </div>
                </div>

                <div
                  id="cancellationcost"
                  v-if="c.cancellationCosts && c.cancellationCosts.length > 0"
                >
                  <div class="content-object">
                    <div class="content-tittle">{{multilanguage.cancellationcost[language]}}</div>
                    <dl>
                      <dd v-if="c.cancellationFreeDate">
                          {{multilanguage.freeuntil[language]}} <span class="font-weight-bold"> {{formatDatetoview(c.cancellationFreeDate)}}</span> 
                      </dd>
                      <dd
                        v-for="(c,index1) in c.cancellationCosts"
                        v-if="c.retail"
                        v-bind:key="index1"
                      >
                        <template v-if="c.retail.value !== 0 && c.retail.value !== '' ">
                          <span
                            class="font-weight-bold"
                          >{{c.retail.value}} {{c.retail.currencyIsoCode }}</span>
                          {{multilanguage.asof[language]}} 
                          <span class="font-weight-bold">{{formatDatetoview(c.gmttime)}}</span>
                        </template>
                      </dd>
                    </dl>
                  </div>
                </div>

                <div id="remarks" v-if="c.remarks && c.remarks.length > 0">
                  <div class="content-object">
                    <div class="content-tittle">{{multilanguage.remarks[language]}}</div>

                    <dl class="collection" style="border:0px;">
                      <dd
                        style="padding-left:0px;"
                        class="collection-item"
                        v-for="(r,index) in c.remarks"
                        v-bind:key="index"
                      >{{ r.text }}</dd>
                    </dl>
                  </div>
                </div>

                <div id="departureinstructions" v-if="c.departureInstructions">
                  <div class="content-object">
                    <div class="content-tittle">{{multilanguage.departureinstructions[language]}}</div>
                    {{c.departureInstructions}}
                  </div>
                </div>
                <div id="arrivalinstructions" v-if="c.arrivalInstructions">
                  <div class="content-object">
                    <div class="content-tittle">{{multilanguage.arrivalinstructions[language]}}</div>
                    {{c.arrivalInstructions}}
                  </div>
                </div>

               
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-else>
      <div class="row ">
        <div class="col-sm-12 col-md-4 cart-align"><i class="material-icons md-64">
remove_shopping_cart
</i></div>
        <div class="col-sm-12 col-md-8 cart-text-align"><h1>{{multilanguage.emptycart[language]}}</h1></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import myCaptcha from "vue-captcha";
import VueRecaptcha from "vue-recaptcha";
import helper from '@/script/helper';

var scrolled = 0;
var globallanguage = "es";

if (globaldata.language != null && globaldata.language !== "") {
  globallanguage = globaldata.language;
}
const langfile = require("../../assets/lang/content.json");
export default {
  name: "Cart",
  data: function() {
    return {
      detalles: {},
      cart: localStorage.qtse_cart
        ? JSON.parse(localStorage.getItem("qtse_cart"))
        : [],
      multilanguage: langfile.multilanguage,
      language: globallanguage,
      windowTop: 0,
      loader:null,
      captchaValidation: globaldata.captchaValidation,
      recaptchatoken: globaldata.recaptchatoken,
      reseolvedCaptcha: false,
      termsaccepted: false,
      activedetail: 0,
      activestyle: 0,
      unmailing:false,
      nobreakdown: globaldata.nobreakdown
    };
  },
  computed: {
    totales: function() {
      var tot = 0;
      var curr;
      for (var j = 0; this.$data.cart.length > j; j++) {
        if (this.$data.cart[j].total && this.$data.cart[j].total.retailPrice) {
          tot = tot + this.$data.cart[j].total.retailPrice.value;
          curr = this.$data.cart[j].total.retailPrice.currencyIsoCode;
        }
      }
      return tot.toFixed(2) + " " + curr;
    }
  },
  components: {
    "my-captcha": myCaptcha,
    VueRecaptcha
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    $("#collapse0").collapse("show");
    this.getTerms();
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },

  methods: {
    getTerms: function() {
      var d = this.$data;
      d.loader = true;
      axios({
        method: "get",
        url: globaldata.baseurl + "/cms/terms",
        params: {
          language: d.language
        }
      })
        .then(function(response) {
          d.loader = null;
          d.detalles = response.data;
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    },
    verify: function() {
      // this.status = "submitting";
      this.$refs.recaptcha.execute();
    },
    onCaptchaVerified: function(recaptchaToken) {
      const _this = this;
      _this.status = "submitting";
      _this.$refs.recaptcha.reset();
      _this.book(recaptchaToken);
    },
    onCaptchaExpired: function() {
      this.$refs.recaptcha.reset();
    },
    captchaOk() {
      this.$data.reseolvedCaptcha = true;
    },
    onScroll(e) {
      if (window.innerWidth > 960) {
        var basket = document.getElementById("basket");
        if (
          (basket !== null && basket !== '') &&
          (basket.getBoundingClientRect().top < 100 ||
          basket.getBoundingClientRect().top < 5)
        ) {
          if (scrolled === 0) {
            scrolled = window.top.scrollY;
          }
        }

        if (scrolled > 0) {
          var minsize = 0;
          if (basket.offsetHeight - screen.height * 0.6 < 0) {
            minsize = 0;
          } else {
            minsize = basket.offsetHeight - screen.height * 0.6;
          }
          var maxsize = document.getElementById("resume").offsetHeight;

          if (window.top.scrollY - scrolled > minsize) {
            if (window.top.scrollY - scrolled <= maxsize) {
              this.windowTop =
                window.top.scrollY - minsize - scrolled < 0
                  ? 0
                  : window.top.scrollY - minsize - scrolled;
            } else {
              this.windowTop = maxsize - minsize < 0 ? 0 : maxsize - minsize;
            }
          } else {
            this.windowTop = 0;
          }
        } else {
          this.windowTop = 0;
        }
      }
    },
formatDatetoview(dtf) {
      if (dtf) {
        let dateArray = String(dtf).split("-");
        let fecha = "";
        switch (globaldata.language) {
          case "es":
            fecha = dateArray[2] + "-" + dateArray[1] + "-" + dateArray[0];
          case "en":
            fecha = dateArray[1] + "-" + dateArray[2] + "-" + dateArray[0];
          default:
            fecha = dateArray[2] + "-" + dateArray[1] + "-" + dateArray[0];
        }
        return fecha;
      }
    },
    deleteItem: function(index) {
      this.$data.cart.splice(index, 1);
      localStorage.setItem("qtse_cart", JSON.stringify(this.$data.cart));
      this.$data.cart = localStorage.qtse_cart
        ? JSON.parse(localStorage.getItem("qtse_cart"))
        : [];
    },
    deleteAll: function() {
      this.$data.cart = [];
      localStorage.setItem("qtse_cart", []);
    },
    book: function(recaptchaToken) {
      var esto = this;
      var d = this.$data;
      var reference = helper.gettimereference();
      if (!d.detalles.terms || (d.detalles.terms && d.termsaccepted)) {
        d.loader = true;
        axios({
          method: "put",
          url: globaldata.baseurl + "/cms/cartbooking",
          timeout: 1000 * 30,
          data: {
            bookings: this.$data.cart,
            captchaToken: recaptchaToken,
            language: d.language,
            mailingUnwanted: d.unmailing
          }
        })
          .then(function(response) {
            d.loader = false;
            esto.procesar(response);
          })
          .catch(function(error) {
            d.loader = false;
            esto.$router.push({ name: "ko", query: { msg: error } });
            console.log(error);
          });
      } else {
        this.setValidationError(
          "termandcondcheck",
          "termandcondcheck-msg",
          this.multilanguage.pleaseacceptterms[this.language]
        );
      }
    },
    procesar: function(response) {
      if (response.data.statusCode === 200) {
        this.$data.cart = [];
        localStorage.setItem("qtse_cart", []);
        this.$router.push({
          name: "ok",
          query: {
            bookingId: response.data.bookingId,
            availableservices: response.data.availableServices,
            paymenturl: response.data.paymentUrl
          }
        });
      } else
        this.$router.push({ name: "ko", query: { msg: response.data.msg } });
    },
    
    setValidationError(field, msgField, msg) {
      var element = document.getElementById(field);

      element.classList.add("is-invalid");
      //   element.classList.add('form-control')
      var att = document.createAttribute("aria-invalid");
      att.value = "true";
      element.setAttributeNode(att);
      element.focus();
      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    openModal: function(htmlText) {
      alert(htmlText);
    },
    formatCancellation: function(viewObject) {
      var htmlText =
        '<table class="table"> ' +
        "<thead>" +
        "<tr>" +
        "<th>" +
        this.$data.multilanguage.to[this.$data.language] +
        "</th>" +
        "<th>" +
        this.$data.multilanguage.type[this.$data.language] +
        "</th> " +
        "<th>" +
        this.$data.multilanguage.amount[this.$data.language] +
        "</th>" +
        "</tr>" +
        "</thead>" +
        "<tbody>";
      alert(viewObject);

      for (var i = 0; i < viewObject.length; i++) {
        htmlText =
          htmlText +
          "<tr>" +
          viewObject[i].text +
          "</tr>" +
          "<td>" +
          viewObject[i].date +
          "</td>" +
          "<td>" +
          viewObject[i].paymentMethod +
          "</td>" +
          '<td width="120px" class="right-align">' +
          '<span style="display: inline-block">' +
          viewObject[i].amount.value +
          viewObject[i].amount.currencyIsoCode +
          "</span>" +
          "</td>" +
          "</tr>";
      }
      htmlText = htmlText + "</tbody>" + "</table>";

      return htmlText;
    },
    formatPayment: function(viewObject) {
      var htmlText = "";

      for (var i = 0; i < viewObject.length; i++) {
        htmlText = htmlText + "<li>" + viewObject[i].text + "</li>";
      }

      return htmlText;
    },
    formatSuplement: function(viewObject) {
      var htmlText = "";

      for (var i = 0; i < viewObject.length; i++) {
        htmlText = htmlText + "<li>" + viewObject[i].text + "</li>";
      }

      return htmlText;
    },
    formatRemarks: function(viewObject) {
      var htmlText = "";

      for (var i = 0; i < viewObject.length; i++) {
        htmlText = htmlText + "<li>" + viewObject[i].text + "</li>";
      }

      return htmlText;
    },
    seticon: function(type) {
      switch (type) {
        case "hotel":
          return "fas fa-bed";

        case "traslado":
          return "fas fa-shuttle-van";

        case "excursion":
          return "fas fa-hiking";

        case "circuito":
          return "fas fa-globe";
        case "otros":
          return "fas fa-luggage-cart";

        default:
          return "";
      }
    },
    viewDetails: function(index) {
      let current = "#collapse" + index;
      let currentnohash = "collapse" + index;
      this.$data.activestyle = index;
    
      $(current).collapse("show");
      setTimeout(function() {
           helper.scrollToId(currentnohash);
      }, 300);
    }
  }
};
</script>
